@import '../variables.css';
/* @import '../App.css'; */



.toggle {
  visibility: hidden;
}
.toggle + label {
  display: flex;
  align-items:center;
  font-size: 1.25em;
  cursor: pointer;
}
.toggle + label::before {
  content: "";
  height: 1em;
  width: 2em;
  border-radius: 1em;
  background-color: var(--toggle-bg);
  border: 1px solid var(--toggle-fg);
  transition: background-color 250ms ease-in-out
}
.toggle + label::after {
  content: "";
  height: 0.8em;
  width: 0.8em;
  border-radius: 1em;
  background-color: var(--toggle-fg);
  left: 0.2em;
  position:absolute;
  transition: background-color 250ms ease-in-out, transform 250ms ease-in-out;
}
.toggle:checked + label::after{
  transform: translateX(100%);

}
