:root {
  --bcg-color: #fafafa;
  --text-color: black;
  --text-color-secondary: rgb(156 163 175);
  --toggle-bg: rgb(238, 238, 176);
  --toggle-fg: orange;
  box-sizing: border-box;
  background-image: none;
  background-repeat: no-repeat;
}

[data-theme='dark']{
  --bcg-color: rgb(18,18,18,0.3);
  --text-color: #fafafa;
  --text-color-secondary: rgb(156 163 175);
  --toggle-bg: rgb(101, 153, 238);
  --toggle-fg: rgb(29, 52, 88);
  --bcg-img: url('../public/oceanbg.jpg');
}

[data-theme='light']{
  --bcg-color: rgb(250,250,250,0.6);
  --text-color: black;
  --text-color-secondary: rgb(95, 94, 95);
  --toggle-bg: rgb(238, 238, 176);
  --toggle-fg: orange;
  --bcg-img: url('../public/sky.jpg');
}
